import React from 'react';
import ReactDOM from 'react-dom';
import { View, Panel, PanelHeaderSimple, Root } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

const App = () => {
	return (
		<Root activeView="main">
    <View id="main" header={false} activePanel="general">
      <Panel separator={false} id="general">
      <PanelHeaderSimple>Тупорылый кликер</PanelHeaderSimple>
      <div align="center">
        <h1 style="font-size: 70px">Прогресс</h1>
        <p><span id="count" style="font-size: 60px;">0</span><span style="font-size: 30px; color: gray;">+<span id="insec" >0</span>/сек</span></p>
      </div>
      </Panel>
      <Panel id="education">
        wait
      </Panel>
    </View>
    <View id="users-search" activePanel="users-search">
      <Panel id="users-search">
        wait
      </Panel>
    </View>
    <View id="check-user-actions" activePanel="check-user-actions">
      <Panel id="check-user-actions">
        wait
      </Panel>
    </View>
  </Root>
	);
}

ReactDOM.render(<App />, document.getElementById('root'));
